import { Component, OnInit } from '@angular/core';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { FormGroup, FormBuilder, Validators, FormArray } from '@angular/forms';
import { CommonService } from '../../services/common.service';
import { ErrorLogService } from '../../services/error-log.service';
import { ActivatedRoute, Router } from '@angular/router';
@Component({
  selector: 'app-report',
  templateUrl: './report.component.html',
  styleUrls: ['./report.component.css']
})
export class ReportComponent implements OnInit {

  reportForm: FormGroup;
  ID: string;
  PatientID: string;
  ReportDetails: any;
  ExtraReportDetails: any;
  PatientName: '';
  Study: '';
  StudyDate: '-';
  currentDate = new Date();
  public preview: boolean = false;
  public previewButton: any = 'Preview';
  public show: boolean = false;
  public buttonName: any = 'Edit';

  constructor(
    private commonService: CommonService,
    private errorLogService: ErrorLogService,
    private route: ActivatedRoute,
    private router: Router,
    public formBuilder: FormBuilder,
  ) {
  }
  ngOnInit() {
    this.ID = this.route.snapshot.paramMap.get("id");
    this.reportForm = this.formBuilder.group({
      _id: [this.ID],
      ReportDetails: this.formBuilder.array([]),
      ExtraReportDetails: this.formBuilder.array([])
    });
    this.index();
  }
  index() {
    this.commonService.getData('study/' + this.ID).subscribe(
      response => {
        if (response.status) {
          this.PatientID = response.data.PatientID;
          this.ReportDetails = Object.keys(response.data.ReportDetails).map(key => ({ field: key, key: response.data.ReportDetails[key].key, value: response.data.ReportDetails[key].value }));
          this.ReportDetails.forEach(object => {
            if (object.field === 'PatientName') {
              this.PatientName = object.value;
            }
            if (object.field === 'StudyDescription') {
              this.Study = object.value;
            }
            if (object.field === 'StudyDate') {
              this.StudyDate = object.value;
            }
            (<FormArray>this.reportForm.get('ReportDetails')).push(this.formBuilder.group({
              key: [object.key],
              value: [object.value]
            }));
            // this.reportForm.addControl(object.key, this.formBuilder.control(object.value, Validators.required));
          });

          if (response.data.ExtraReportDetails !== undefined) {
            this.ExtraReportDetails = Object.keys(response.data.ExtraReportDetails).map(key => ({ field: key, key: response.data.ExtraReportDetails[key].key, value: response.data.ExtraReportDetails[key].value }));
            this.ExtraReportDetails.forEach(object => {
              (<FormArray>this.reportForm.get('ExtraReportDetails')).push(this.formBuilder.group({
                key: [object.key],
                value: [object.value]
              }));
            });
          }
        } else {
          this.errorLogService.handleError(response.message)
          this.router.navigate(['/']);
        }
      },
      error => this.errorLogService.handleError(error)
    );
  }
  reportDetailsSubmit() {
    // var ReportDetails = {};
    // this.allDetails.forEach(object => {
    //   ReportDetails[object.field] = { 'key': object.key, 'value': this.reportForm.controls[object.field].value };
    // });
    // var NewField = this.reportForm.get('NewField').value;
    // var ExtraReportDetails = {};
    // NewField.forEach(object => {
    //   ExtraReportDetails[object.key.replace(" ", "")] = { 'key': object.key, 'value': object.value };
    // });
    // const AllDetails = Object.assign({}, ReportDetails, ExtraReportDetails);
    var ReportDetailsValue = this.reportForm.get('ReportDetails').value;
    var ExtraReportDetails = {};
    ReportDetailsValue.forEach(object => {
      if (object.key) {
        ExtraReportDetails[object.key.replace(" ", "")] = { 'key': object.key, 'value': object.value };
      }
    });
    this.commonService.updateData('report/edit/' + this.reportForm.controls['_id'].value, { 'ReportDetails': ExtraReportDetails }).subscribe(
      response => {
        if (response.status) {
          this.errorLogService.handleSuccess(response.message);
          // this.router.navigate(['/']);
        } else {
          this.errorLogService.handleError(response.message)
        }
      },
      error => this.errorLogService.handleError(error)
    );
  }
  extraReportDetailsSubmit() {
    var ExtraReportDetailsValue = this.reportForm.get('ExtraReportDetails').value;
    var ExtraExtraReportDetails = {};
    ExtraReportDetailsValue.forEach(object => {
      if (object.key) {
        ExtraExtraReportDetails[object.key.replace(" ", "")] = { 'key': object.key, 'value': object.value };
      }
    });
    this.commonService.updateData('report/edit/' + this.reportForm.controls['_id'].value, { 'ExtraReportDetails': ExtraExtraReportDetails }).subscribe(
      response => {
        if (response.status) {
          this.errorLogService.handleSuccess(response.message);
          // this.router.navigate(['/']);
        } else {
          this.errorLogService.handleError(response.message)
        }
      },
      error => this.errorLogService.handleError(error)
    );
  }
  addField(): void {
    const arrayControl = <FormArray>this.reportForm.get('ReportDetails');
    arrayControl.push(this.formBuilder.group({
      key: [''],
      value: ['']
    }));
  }
  removeField(index: number): void {
    const arrayControl = <FormArray>this.reportForm.get('ReportDetails');
    arrayControl.removeAt(index);
    this.reportDetailsSubmit();
  }
  addDetails(): void {
    const arrayControl = <FormArray>this.reportForm.get('ExtraReportDetails');
    arrayControl.push(this.formBuilder.group({
      key: [''],
      value: ['']
    }));
  }
  removeDetails(index: number): void {
    const arrayControl = <FormArray>this.reportForm.get('ExtraReportDetails');
    arrayControl.removeAt(index);
    this.reportDetailsSubmit();
  }
  editToggle() {
    this.show = !this.show;
    if (this.show)
      this.buttonName = "Done";
    else
      this.buttonName = "Edit";
  }
  previewToggle() {
    this.preview = !this.preview;
    if (this.preview)
      this.previewButton = "Edit Report";
    else
      this.previewButton = "Preview";
  }
  dropReportDetails(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.reportForm.get('ReportDetails')['controls'], event.previousIndex, event.currentIndex);
    moveItemInArray(this.reportForm.get('ReportDetails').value, event.previousIndex, event.currentIndex);
    this.reportDetailsSubmit();
  }
  dropExtraReportDetails(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.reportForm.get('ExtraReportDetails')['controls'], event.previousIndex, event.currentIndex);
    moveItemInArray(this.reportForm.get('ExtraReportDetails').value, event.previousIndex, event.currentIndex);
    this.extraReportDetailsSubmit();
  }

}
