import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { SharedModule } from './shared/shared.module';

import { HeaderComponent } from './components/layout/header/header.component';
import { FooterComponent } from './components/layout/footer/footer.component';
import { LoginComponent } from './components/auth/login/login.component';
import { ForgotPasswordComponent } from './components/auth/forgot-password/forgot-password.component';
import { ChangePasswordComponent } from './components/auth/change-password/change-password.component';
import { StudyComponent } from './components/study/study.component';
import { AddStudyComponent } from './components/study/study.component';
import { EditStudyComponent } from './components/study/study.component';
import { ReportComponent } from './components/report/report.component';
import { PageNotFoundComponent } from './components/page-not-found/page-not-found.component';

const routes: Routes = [
  { path: 'page-not-found', component: PageNotFoundComponent, data: { title: 'Page not found' } },
  { path: '', component: StudyComponent, data: { title: 'Study' } },
  { path: 'study', component: StudyComponent, data: { title: 'Study' } },
  { path: 'report/:id', component: ReportComponent, data: { title: 'Report' } },

  { path: '**', redirectTo: '/page-not-found' }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes),
    SharedModule
  ],
  exports: [
    RouterModule,
    HeaderComponent,
    FooterComponent,
    LoginComponent,
    ForgotPasswordComponent,
    ChangePasswordComponent,
    StudyComponent,
    AddStudyComponent,
    EditStudyComponent,
    ReportComponent,
    PageNotFoundComponent
  ],
  declarations: [
    HeaderComponent,
    FooterComponent,
    LoginComponent,
    ForgotPasswordComponent,
    ChangePasswordComponent,
    StudyComponent,
    AddStudyComponent,
    EditStudyComponent,
    ReportComponent,
    PageNotFoundComponent
  ],
  entryComponents: [
    AddStudyComponent,
    EditStudyComponent
  ],
  providers: [
  ],
})
export class AppRoutingModule { }
