import { Component, OnInit, ViewChild, Inject } from '@angular/core';
import { MatTableDataSource, MatPaginator, MatSort, MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { animate, state, style, transition, trigger } from '@angular/animations';
import { FormGroup, FormBuilder, FormArray } from '@angular/forms';
import { CommonService } from '../../services/common.service';
import { ErrorLogService } from '../../services/error-log.service';
@Component({
  selector: 'app-study',
  templateUrl: './study.component.html',
  styleUrls: ['./study.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({ height: '0px', minHeight: '0', display: 'none' })),
      state('expanded', style({ height: '*' })),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ]
})
export class StudyComponent implements OnInit {
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  title = "Study";
  reports = [];
  columns = [];
  count = [];
  dataSource: any;
  columnsToDisplay = ['PatientID', 'Action'];
  // columnsToDisplay = ['PatientID', 'Patient', 'Gender', 'Age', 'Study Description', 'Modality', 'Comments', 'Action'];

  constructor(
    public formBuilder: FormBuilder,
    private commonService: CommonService,
    private errorLogService: ErrorLogService,
    public dialog: MatDialog
  ) {
  }
  ngOnInit() {
    this.index();
  }

  index() {
    this.commonService.getData('study').subscribe(
      response => {
        this.count = response.data.length;
        if (response.status) {
          // this.dataSource = new MatTableDataSource(response.data);
          // this.dataSource.sort = this.sort;
          // this.dataSource.paginator = this.paginator;

          this.columns[0] = Object.keys(response.data[0].PatientDetails).map(key => ({ field: key, key: response.data[0].PatientDetails[key].key, value: response.data[0].PatientDetails[key].value }));

          response.data.forEach(element => {
            this.reports[element._id] = Object.keys(element.PatientDetails).map(key => ({ field: key, key: element.PatientDetails[key].key, value: element.PatientDetails[key].value }));
            this.reports[element._id]['PatientID'] = element.PatientID;
            // this.reports[element._id]['Files'] = Object.keys(element.FileDetails).map(key => (element.FileDetails[key]));
            this.reports[element._id]['Files'] = Object.keys(element.FileDetails).map(key => (
              Object.keys(element.FileDetails[key]).map(keys => ({ key: element.FileDetails[key][keys].key, value: element.FileDetails[key][keys].value }))
            ));
          });
        }
      },
      error => this.errorLogService.handleError(error)
    );
  }

  openAddStudy(): void {
    const dialogRef = this.dialog.open(AddStudyComponent, {
      width: '50%'
    });

    dialogRef.afterClosed().subscribe(result => {
      this.index();
    });
  }

  openEditStudy(ID): void {
    const dialogRef = this.dialog.open(EditStudyComponent, {
      width: '75%',
      // height: '600px',
      data: { id: ID }
    });

    dialogRef.afterClosed().subscribe(result => {
      this.index();
    });
  }

}
/****************************************************************************************** */
@Component({
  selector: 'app-add-study',
  templateUrl: './add-study.html',
  styleUrls: ['./study.component.css'],
})
export class AddStudyComponent {
  title = "Study";
  fileToUpload: string[] = [];

  constructor(
    public dialogRef: MatDialogRef<AddStudyComponent>,
    private commonService: CommonService,
    private errorLogService: ErrorLogService, ) {
  }

  onSubmit(files) {
    this.fileToUpload = [] = [];
    for (var i = 0; i < files.length; i++) {
      this.fileToUpload.push(files[i]);
    }
    const formData: FormData = new FormData();
    for (var i = 0; i < this.fileToUpload.length; i++) {
      formData.append("photo", this.fileToUpload[i]);
    }
    this.commonService.addData('study/add', formData).subscribe(
      response => {
        if (response.status) {
          this.errorLogService.handleSuccess(response.message);
          this.close();
        } else {
          this.errorLogService.handleError(response.message)
        }
      },
      error => this.errorLogService.handleError(error)
    );
  }

  close() {
    this.dialogRef.close();
  }

}
/****************************************************************************************** */
@Component({
  selector: 'app-edit-study',
  templateUrl: './edit-study.html',
  styleUrls: ['./study.component.css'],
})
export class EditStudyComponent {
  title = "Study";
  reportForm: FormGroup;
  ID: string;
  constructor(
    public dialogRef: MatDialogRef<EditStudyComponent>,
    @Inject(MAT_DIALOG_DATA) data,
    private commonService: CommonService,
    public formBuilder: FormBuilder,
    private errorLogService: ErrorLogService, ) {
    this.ID = data.id;
    this.reportForm = this.formBuilder.group({
      _id: [this.ID],
      PatientDetails: this.formBuilder.array([])
    });
  }

  ngOnInit() {
    this.commonService.getData('study/' + this.ID).subscribe(
      response => {
        if (response.status) {
          var PatientDetails = Object.keys(response.data.PatientDetails).map(key => ({ field: key, value: response.data.PatientDetails[key].value, key: response.data.PatientDetails[key].key }));
          PatientDetails.forEach(object => {
            (<FormArray>this.reportForm.get('PatientDetails')).push(this.formBuilder.group({
              key: [object.key],
              value: [object.value]
            }));
          });
        } else {
          this.errorLogService.handleError(response.message)
        }
      },
      error => this.errorLogService.handleError(error)
    );
  }

  patientDetailsSubmit() {
    var PatientDetailsValue = this.reportForm.get('PatientDetails').value;
    var ExtraPatientDetails = {};
    PatientDetailsValue.forEach(object => {
      if (object.key) {
        ExtraPatientDetails[object.key.replace(" ", "")] = { 'key': object.key, 'value': object.value };
      }
    });
    this.commonService.updateData('study/edit/' + this.reportForm.controls['_id'].value, { 'PatientDetails': ExtraPatientDetails }).subscribe(
      response => {
        if (response.status) {
          this.errorLogService.handleSuccess(response.message);
          this.close();
        } else {
          this.errorLogService.handleError(response.message)
        }
      },
      error => this.errorLogService.handleError(error)
    );
  }

  close() {
    this.dialogRef.close();
  }

  addField(): void {
    const arrayControl = <FormArray>this.reportForm.get('PatientDetails');
    arrayControl.push(this.formBuilder.group({
      key: [''],
      value: ['']
    }));
  }

  removeField(index: number): void {
    const arrayControl = <FormArray>this.reportForm.get('PatientDetails');
    arrayControl.removeAt(index);
  }

}
